import { AuthState } from "models/Authentication";
import { AuthActionsTypes, NEED_VERIFICATION, SET_ERROR, SET_LOADING, SET_SUCCESS, SET_USER, SIGN_OUT, UPDATE_USER } from "store/types/types";

const initialState: AuthState = {
    user: null,
    authenticated: false,
    loading: false,
    error: '',
    needVerification: false,
    success: ''
}

// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initialState, action: AuthActionsTypes) => {
    switch(action.type){
        case SET_USER:
            return {
                ...state,
                user: action.payload,
                authenticated: true,
            }
        case SIGN_OUT:
            return {
                ...state,
                user: null,
                authenticated: false,
                loading: false
            }
        case SET_LOADING:
            return {
                ...state,
                loading: action.payload
            }
        case SET_ERROR:
            return {
                ...state,
                error: action.payload
            }
        case SET_SUCCESS:
            return {
                ...state,
                success: action.payload
            }
        case NEED_VERIFICATION:
            return {
                ...state,
                needVerification: true
            }
        case UPDATE_USER:
            return {
                ...state,
                user: action.payload
            }
        default:
            return state;
        
    }
}