import { CourseState } from "models/CourseModel";
import { CoursesActionTypes, GET_COURSES} from "store/types/types";

const initialState: CourseState = {
    courses: null,
}

// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initialState, action: CoursesActionTypes) => {
   switch(action.type){
       case GET_COURSES:
           return {
               ...state,
               courses: action.payload,
           }
       default:
           return state;
       
   }
}