import { database } from "database/firebase";
import { createUserWithEmailAndPassword, getAuth, signInWithEmailAndPassword, signOut } from "firebase/auth";
import { doc, getDoc, setDoc, updateDoc } from "firebase/firestore";
import { SignInData, SignUpData, User } from "models/Authentication";
import { ThunkAction } from "redux-thunk";
import { RootState } from "store";
import { AuthActionsTypes, NEED_VERIFICATION, SET_ERROR, SET_LOADING, SET_SUCCESS, SET_USER, SIGN_OUT, UPDATE_USER } from "store/types/types";


export const signUp = (data: SignUpData, onError: () => void): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        const auth = getAuth();
        createUserWithEmailAndPassword(auth, data.email, data.password)
            .then((userCredential) => {
        const userData: User = {
        email: data.email,
        id: userCredential.user.uid,
        createdAt: new Date(),
        coursesDone: 0
    };
    setDoc(doc(database, "users", userData.id), userData);
    dispatch({
        type: SET_USER,
        payload: userData
    });
  })
  .catch((error) => {
    onError();
    dispatch({
        type: SET_ERROR,
        payload: "Użytkownik z takim emailem już istnieje"
    });
  });
    }
}

export const setError = (errorMessage: string): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            dispatch({
                type: SET_ERROR,
                payload: errorMessage
            });

        }
        catch (error: any) {
            console.log(error);
        }
    }
}

export const signIn = (data: SignInData, onError: () => void): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            const auth = getAuth();
            signInWithEmailAndPassword(auth, data.email, data.password).catch((error) => {
                onError();
                console.log(error);
                dispatch(setError("Nie ma takiego użytkownika"));
            })
        }
        catch (error: any) {
            onError();
            console.log(error);
            dispatch(setError(error.message));
        }
    }
}

export const signout = (): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            dispatch(setLoading(true));
            const auth = getAuth();
            signOut(auth)
            dispatch({
                type: SIGN_OUT
            });
        }
        catch (error: any) {
            console.log(error);
            dispatch(setError(error.message));
            dispatch(setLoading(false));

        }
    }
}


// Get user by id
export const getUserById = (id: string): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            const docRef = doc(database, "users", id);
const user = await getDoc(docRef);
            //const user = await database.collection("users").doc(id).get();
            if(user){
                const userData = user.data() as User;
                dispatch({
                    type: SET_USER,
                    payload: userData
                });
            }
        }
        catch (error: any) {
            console.log(error);
        }
    }
}

