export const sendPasswordResetEmail = (email: string, successMessage: string): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            //auth.sendPasswordResetEmail(email)
            dispatch(setSuccess(successMessage))
        }
        catch (error: any) {
            console.log(error);
            dispatch(setError(error.message))
        }
    }
}

export const updateUser = (user: User): ThunkAction<void, RootState, null, AuthActionsTypes> => {
    return async dispatch => {
        try{
            const docRef = doc(database, "users", user.id);
            await updateDoc(docRef, {
                ...user
            });

            dispatch({
                type: UPDATE_USER,
                payload: user
            });
        }
        catch (error: any) {
            console.log(error);
            dispatch(setError(error.message))
        }
    }
}

