import * as React from "react";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepButton from "@mui/material/StepButton";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import "App.scss";
import { CourseStep } from "components/CourseStep";
import { DrawerMenu } from "components/DrawerMenu";
import { Link } from "react-router-dom";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";

const steps = [
  "Wybór środowiska",
  "Przydatne rozszerzenia",
  "Uruchamianie plików js",
];

const kroki = [
  {
    nazwa: "Wybór środowiska",
    url: "https://www.youtube.com/embed/4TRgHquq_Xo",
  },
  {
    nazwa: "Przydatne rozszerzenia",
    url: "https://www.youtube.com/embed/QEM6lccG0Kk",
  },
  {
    nazwa: "Uruchamianie plików js",
    url: "https://www.youtube.com/embed/TO3m-iVEpoc",
  },
];

interface IntroProps {}

export const Intro: React.FC<IntroProps> = ({}) => {
  const [activeStep, setActiveStep] = React.useState(0);
  const [completed, setCompleted] = React.useState<{
    [k: number]: boolean;
  }>({});

  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? // It's the last step, but not all steps have been completed,
          // find the first step that has been completed
          steps.findIndex((step, i) => !(i in completed))
        : activeStep + 1;
    setActiveStep(newActiveStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStep = (step: number) => () => {
    setActiveStep(step);
  };

  const handleComplete = () => {
    const newCompleted = completed;
    newCompleted[activeStep] = true;
    setCompleted(newCompleted);
    handleNext();
  };

  const handleReset = () => {
    setActiveStep(0);
    setCompleted({});
  };
  return (
    <div className="intro-container">
      <div className="course-header">
        <Link to="/dashboard">
          <ArrowBackIosIcon color="primary" />
        </Link>
        <h1>Wstęp</h1>
      </div>
      <Box sx={{ width: "100%" }}>
        <Stepper nonLinear activeStep={activeStep}>
          {steps.map((label, index) => (
            <Step key={label} completed={completed[index]}>
              <StepButton color="inherit" onClick={handleStep(index)}>
                {label}
              </StepButton>
            </Step>
          ))}
        </Stepper>
        <div>
          {allStepsCompleted() ? (
            <React.Fragment>
              <Box sx={{ mt: 2, mb: 1 }}>
                <div className="intro-finished">
                  <p>
                    Wszystkie kroki ze wstępu już za tobą!
                    <br />
                    Możesz przejść do dalszej części kursu klikając przycisk
                    poniżej
                  </p>
                  <DrawerMenu />
                </div>
              </Box>
              <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                <Box sx={{ flex: "1 1 auto" }} />
                <Button onClick={handleReset}>Resetuj</Button>
              </Box>
            </React.Fragment>
          ) : (
            <React.Fragment>
              <Box sx={{ mt: 2, mb: 1 }}>
                <CourseStep
                  nazwa={kroki[activeStep].nazwa}
                  url={kroki[activeStep].url}
                />
              </Box>
              <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                <Button
                  color="inherit"
                  disabled={activeStep === 0}
                  onClick={handleBack}
                  sx={{ mr: 1 }}
                >
                  Cofnij
                </Button>
                <Box sx={{ flex: "1 1 auto" }} />
                <Button onClick={handleNext} sx={{ mr: 1 }}>
                  Następny
                </Button>
                {activeStep !== steps.length &&
                  (completed[activeStep] ? (
                    <Typography
                      variant="caption"
                      sx={{ display: "inline-block" }}
                    >
                      Krok {activeStep + 1} wykonany
                    </Typography>
                  ) : (
                    <Button onClick={handleComplete}>
                      {completedSteps() === totalSteps() - 1
                        ? "Koniec"
                        : "Ukończ etap"}
                    </Button>
                  ))}
              </Box>
            </React.Fragment>
          )}
        </div>
      </Box>
    </div>
  );
};
