import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import "App.scss";
import * as React from "react";
import { Link } from "react-router-dom";

interface HomeProps {}

export const Home: React.FC<HomeProps> = ({}) => {
  return (
    <div className="home-container">
      <p className="home-title">
        Naucz się <strong>J</strong>ava<strong>S</strong>cript
      </p>
      <Link to="/signup">
        <Button
          className="create-account-btn home-button"
          color="secondary"
          variant="contained"
        >
          Start
        </Button>
      </Link>
      <div className="home-accordion">
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>Dlaczego kurs Java Script?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              JavaScript (JS) to język front-endowy, dzięki któremu nauczysz się
              programować to, co użytkownik widzi w oknie przeglądarki. Jest to
              jeden z najważniejszych języków programowania na świecie.
              Korzystają z niego takie firmy, jak Facebook, Uber czy PayPal.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel2a-content"
            id="panel2a-header"
          >
            <Typography>Dlaczego warto?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              JS ma szerokie zastosowanie w tworzeniu stron internetowych,
              aplikacji mobilnych, a nawet gier i Internetu Rzeczy np. SmartTV
              czy zegarków połączonych z telefonem. Jego powszechność i
              uniwersalność sprawiają, że zapotrzebowanie na specjalistów
              JavaScript ciągle rośnie.
            </Typography>
          </AccordionDetails>
        </Accordion>
      </div>
    </div>
  );
};
