import { User } from "models/Authentication";
import { Course } from "models/CourseModel";

export const SET_USER = "SET_USER";
export const SIGN_OUT = "SIGN_OUT";
export const SET_LOADING = "SET_LOADING";
export const SET_ERROR = "SET_ERROR";
export const NEED_VERIFICATION = "NEED_VERIFICATION";
export const SET_SUCCESS = "SET_SUCCESS";
export const GET_COURSES = "GET_COURSES";
export const UPDATE_USER = "UPDATE_USER";

// Action types

interface SetUserAction {
    type: typeof SET_USER,
    payload: User
}

interface SetLoadingAction {
    type: typeof SET_LOADING,
    payload: boolean
}

interface SignOutAction {
    type: typeof SIGN_OUT,
}

interface SetErrorAction {
    type: typeof SET_ERROR,
    payload: string
}

interface SetSuccessAction {
    type: typeof SET_SUCCESS,
    payload: string
}

interface SetVerifivationAction {
    type: typeof NEED_VERIFICATION,
}


interface GetCoursesAction{
    type: typeof GET_COURSES;
    payload: Course[];
}

interface UpadteUser {
    type: typeof UPDATE_USER;
    payload: User
}

export type AuthActionsTypes = SetUserAction | SetLoadingAction | SignOutAction | SetErrorAction | SetSuccessAction | SetVerifivationAction | UpadteUser;
export type CoursesActionTypes = GetCoursesAction;

export type AppActions = AuthActionsTypes | CoursesActionTypes;