import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import { DrawerMenu } from "components/DrawerMenu";
import { ProgressCircle } from "components/ProgressCircle";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "store";
import { getCourses } from "store/actions/courseActions";
import { updateUser } from "store/actions/authActions";

interface DashboardProps {}

export const Dashboard: React.FC<DashboardProps> = ({}) => {
  const { user } = useSelector((state: RootState) => state.auth);
  const { courses } = useSelector((state: RootState) => state.courses);
  const [loading, setLoading] = useState(true);
  const [result, setResult] = useState(0);
  const userEmail = user?.email.substring(0, user?.email.indexOf("@"));
  const action = useDispatch();

  useEffect(() => {
    action(getCourses());
    calculatePercentage();
    setLoading(false);
  }, []);
  useEffect(() => {
    calculatePercentage();
  }, [courses]);

  const handleResetClick = () => {
    if (user) {
      user.coursesDone = 0;
      action(updateUser(user));
      window.location.reload();
    }
  };

  // useEffect(() => {
  //   console.log(user?.coursesDone);
  // }, [handleResetClick]);

  const calculatePercentage = () => {
    if (user && courses) {
      setResult(Math.floor((user.coursesDone / courses.length) * 100));
    }
  };

  if (loading) {
    return (
      <>
        <p>ŁADOWANIE...</p>
        <Box sx={{ paddingTop: "100px" }}>
          <CircularProgress size={100} />
        </Box>
      </>
    );
  }

  return (
    <div className="dashboard-container">
      <div className="welcome-container">
        <h1>Witaj!</h1>
        <p>{userEmail}</p>
      </div>
      <div className="progress-container">
        <h3>Status ukończenia kursu</h3>
        <ProgressCircle progressValue={result} />
        <h1>{result}%</h1>
        <DrawerMenu />
      </div>
      <Button onClick={handleResetClick} variant="contained" color="secondary">
        Wyzeruj postępy
      </Button>
    </div>
  );
};
