import { database } from "database/firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";
import { Course } from "models/CourseModel";
import { ThunkAction } from "redux-thunk";
import { RootState } from "store";
import { CoursesActionTypes, GET_COURSES } from "store/types/types";

export const getCourses = (): ThunkAction<void, RootState, null, CoursesActionTypes> => {
    return async dispatch => {
        try{
            const sortedCourses = query(collection(database, "courses"), orderBy("courseNumber"));
            const ref = await getDocs(sortedCourses);
            if(ref){
                let coursesList: Course[] = [];
                
                ref.forEach((doc) => 
                    (coursesList.push(doc.data() as Course))
                );
               dispatch({
                   type: GET_COURSES,
                   payload: coursesList
               });
           }

        }
        catch (error: any) {
            console.log(error);
        }
    }
}