import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import TextField from "@mui/material/TextField";
import { Course } from "models/CourseModel";
import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { RootState } from "store";
import { updateUser } from "store/actions/authActions";
import DoneIcon from "@mui/icons-material/Done";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert, { AlertProps } from "@mui/material/Alert";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import { Link } from "react-router-dom";

interface CoursePageProps {}

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

export const CoursePage: React.FC<CoursePageProps> = () => {
  const { courses } = useSelector((state: RootState) => state.courses);
  const { user } = useSelector((state: RootState) => state.auth);
  const { id } = useParams<{ id: string }>();
  const [course, setCourse] = useState<Course | null>(null);
  const [loading, setLoading] = useState(true);
  const [nextCourse, setNextCourse] = useState<Course | null>(null);
  const [previousCourse, setPreviousCourse] = useState<Course | null>(null);
  const [taskDone, setTaskDone] = useState<Boolean>(false);
  const [taskText, setTaskText] = useState<string>("");
  const [open, setOpen] = useState(false);
  const action = useDispatch();
  const area = useRef<HTMLTextAreaElement>();

  useEffect(() => {
    const foundCourse = courses && courses.find((course) => course.id === id);
    foundCourse && setCourse(foundCourse);
  }, [courses]);

  useEffect(() => {
    if (course && courses) {
      const nextCourse = courses.find(
        (crs) => crs.courseNumber === course.courseNumber + 1
      );
      const previousCourse = courses.find(
        (crs) => crs.courseNumber === course.courseNumber - 1
      );
      nextCourse && setNextCourse(nextCourse);
      previousCourse && setPreviousCourse(previousCourse);
      setTaskDone(checkUserDoneCourse(course.courseNumber));
      setLoading(false);
    }
  }, [course]);

  const handleNextCourseClick = () => {
    if (nextCourse && taskDone) {
      setCourse(nextCourse);
      setTaskDone(checkUserDoneCourse(nextCourse.courseNumber));
      setTaskText("");
    }
  };

  const handlePreviousCourseClick = () => {
    if (previousCourse) {
      setCourse(previousCourse);
      setTaskDone(true);
    }
  };

  const checkUserDoneCourse = (courseId: number): boolean => {
    return user ? user.coursesDone >= courseId : false;
  };

  const handleTestTaskCode = () => {
    const begin = `function test() {`;
    const serverCode1 = course?.serverCode1;
    const userCode = area.current?.value;
    const serverCode2 = course?.serverCode2;
    const run = `test();`;
    const res = begin + serverCode1 + userCode + serverCode2 + run;
    let result: Boolean = false;
    try {
      result = eval(res);
    } catch (e) {
      result = false;
    }
    if (result) {
      if (user && course) {
        if (user.coursesDone <= course.courseNumber) {
          user.coursesDone = course.courseNumber;
          action(updateUser(user));
          user?.coursesDone === 10 && setOpen(true);
        }
      }
      setTaskDone(result);
    }
  };

  const handleClose = (
    event: React.SyntheticEvent | React.MouseEvent,
    reason?: string
  ) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  if (loading) {
    return (
      <>
        <p>ŁADOWANIE...</p>
        <Box sx={{ paddingTop: "100px" }}>
          <CircularProgress size={100} color="secondary" />
        </Box>
      </>
    );
  }

  return (
    <div className="course-container">
      <div className="course-header">
        <Link to="/dashboard">
          <ArrowBackIosIcon color="primary" />
        </Link>
        <h1>
          {course && course.courseNumber}. {course?.name}
        </h1>
      </div>
      <iframe
        src={course?.url}
        title="YouTube video player"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
        className="movie-frame"
      ></iframe>
      <h3>Zadanie:</h3>
      <p>{course?.content}</p>
      {taskDone ? (
        <div className="task-done-container">
          <p>Zadanie zostało prawidłowo wykonane!</p>
          <DoneIcon color="success" style={{ fontSize: 70 }} />
        </div>
      ) : (
        <TextField
          id="outlined-multiline-static"
          label="Miejsce na kod"
          multiline
          rows={8}
          className="course-text-field"
          inputRef={area}
        />
      )}

      <div className="course-buttons">
        {!taskDone && (
          <Button
            variant="outlined"
            onClick={handleTestTaskCode}
            color="secondary"
          >
            Sprawdź
          </Button>
        )}

        <div className="course-nav-buttons">
          {course && course.courseNumber !== 1 && (
            <Button
              variant="contained"
              onClick={handlePreviousCourseClick}
              color="secondary"
            >
              Poprzedni kurs
            </Button>
          )}
          {course &&
            checkUserDoneCourse(course.courseNumber) &&
            course.courseNumber !== 10 && (
              <Button variant="contained" onClick={handleNextCourseClick}>
                Następny kurs
              </Button>
            )}
        </div>
      </div>
      {user && (
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            Gratulacje, udało ci sie ukończyć wszystkie kursy
          </Alert>
        </Snackbar>
      )}
    </div>
  );
};
