import { yupResolver } from "@hookform/resolvers/yup";
import { Button } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { FormProvider, SubmitHandler, useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { RootState } from "store";
import { BackArrowButton } from "../../components/BackArrowButton";
import { PasswordInput } from "../../components/PasswordInput";
import { TextInput } from "../../components/TextInput";
import { SignInData } from "../../models/Authentication";
import { setError, signIn } from "../../store/actions/authActions";
import { signUpSchema } from "./Schema";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

interface SignInProps {}

export const SignIn: React.FC<SignInProps> = ({}) => {
  const action = useDispatch();

  const { error } = useSelector((state: RootState) => state.auth);
  const [loading, setLoading] = useState(false);
  const methods = useForm<SignInData>({
    resolver: yupResolver(signUpSchema),
  });

  useEffect(() => {
    return () => {
      if (error) {
        action(setError(""));
      }
    };
  }, [error, action]);

  const handleClose = (event?: React.SyntheticEvent, reason?: string) => {
    if (reason === "clickaway") {
      return;
    }
    action(setError(""));
  };

  const formSubmitHandler: SubmitHandler<SignInData> = (data: SignInData) => {
    setLoading(true);
    action(signIn(data, () => setLoading(false)));
    if (error) {
      action(setError(error));
    }
  };

  return (
    <div className="wrapper">
      <div className="container">
        <div className="header">
          <BackArrowButton path="/home" />
          <h1>Logowanie</h1>
        </div>
        {/* <p className="description-text">
          Zarejestruj się przy użyciu innej platformy
        </p>
        <div className="buttons-container">
          <Button className="icon-button" color="secondary" variant="contained">
            <i className="fab fa-google"></i>
          </Button>
          <Button className="icon-button" color="secondary" variant="contained">
            <i className="fab fa-github"></i>
          </Button>
        </div> */}
        <FormProvider {...methods}>
          <form
            action=""
            className="signin-form"
            onSubmit={methods.handleSubmit(formSubmitHandler)}
          >
            <TextInput
              name="email"
              type="email"
              variant="outlined"
              label="E-mail"
              placeholder="email@test.com"
            />
            <PasswordInput name="password" variant="outlined" label="Hasło" />
            <Button
              className="create-account-btn"
              color="secondary"
              variant="contained"
              type="submit"
            >
              Zaloguj się
            </Button>
          </form>
        </FormProvider>
        <div className="footer">
          <p className="description-text">Nie masz konta?</p>
          <Link to="/signup">
            <strong>
              <p className="redirect-button">Zarejestruj się</p>
            </strong>
          </Link>
        </div>
      </div>
      <Snackbar open={!!error} autoHideDuration={3000} onClose={handleClose}>
        <MuiAlert onClose={handleClose} severity="error">
          {error}
        </MuiAlert>
      </Snackbar>
    </div>
  );
};
